import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFirestore, AngularFirestoreDocument} from '@angular/fire/firestore';
import {User} from '../../models/user.model';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {

  private user = this.auth.user$;

  constructor(
    public auth: AuthService,
    public  aauth: AngularFireAuth,
    private router: Router,
    private afs: AngularFirestore) { }

  backToHome() {
    this.router.navigate(['/']);
  }

  async deleteUserAccountFromDb(user) {
    const userRef: AngularFirestoreDocument<User> = this.afs.doc(`users/${user.uid}`);
    await userRef.delete();
  }

  async deleteAccount2() {
    try {
      const user = this.aauth.auth.currentUser;

      // Get the FS document accroding to the user's UID
      const userRef: AngularFirestoreDocument<User> = this.afs.doc(`users/${user.uid}`);

      // Delete the entry from the db
      await userRef.delete();
      console.log('Your account have been successfully remove from the db');

      // Delete the current user from users
      await this.aauth.auth.currentUser.delete();
      console.log('Your account has been successfully deleted!');
      this.backToHome();
    } catch (e) {
      console.log(e);
    }
  }


  ngOnInit() {
  }




}
